import React, { useEffect, useState } from "react";
import {
  motion,
  MotionValue,
  useMotionValue,
  useTransform,
} from "framer-motion";
import "./App.css";

export default function App() {
  const [thingToDo, setThingToDo] = useState<string | undefined>();

  useEffect(() => {
    (async () => {
      try {
        const resp = await fetch("https://www.boredapi.com/api/activity");
        const { activity }: { activity: string } = await resp.json();
        setThingToDo(activity.toLowerCase());
      } catch (error) {
        setThingToDo(
          "fix the API Albert is using on this website to show a new activity every time"
        );
      }
    })();
  }, []);

  const mouseX = useMotionValue(200);
  const mouseY = useMotionValue(200);
  const rotateX = useTransform(mouseY, [0, 400], [45, -45]);
  const rotateY = useTransform(mouseX, [0, 400], [-45, 45]);
  useEffect(() => {
    // TODO throttle this function.
    function onMouseMove(e: MouseEvent) {
      const isMobile = e.movementX === 0 && e.movementY === 0;
      if (isMobile) {
        // TODO Add animation for mobile
        return;
      }

      mouseX.set(e.clientX);
      mouseY.set(e.clientY);
    }

    window.addEventListener("mousemove", onMouseMove);

    return () => {
      window.removeEventListener("mousemove", onMouseMove);
    };
  }, []);

  return (
    <>
      <div className="container">
        <Avatar rotateX={rotateX} rotateY={rotateY} />
        <div className="intro-container">
          Hey, I'm{" "}
          <motion.span whileHover={{ color: Colors.orange, scale: 1.2 }}>
            Albert
          </motion.span>
          o Morabito. I mostly create Web-related products for a living.
        </div>
      </div>
      <WIPHeader />
      <ActivitySuggestion thingToDo={thingToDo} />
    </>
  );
}

function Avatar(props: {
  rotateX: MotionValue<number>;
  rotateY: MotionValue<number>;
}) {
  const { rotateX, rotateY } = props;

  return (
    <motion.div
      className={"avatar-container"}
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      style={{ perspective: 400 }}
    >
      <motion.img
        className="avatar-img"
        src="https://avatars.githubusercontent.com/u/33761650?v=4"
        alt="A photo of Albert's face"
        style={{ rotateX, rotateY }}
        whileHover={{
          rotate: 360,
          border: `4px solid ${Colors.orange}`,
        }}
        whileTap={{ rotate: 360, borderColor: Colors.orange }}
        transition={{
          type: "spring",
        }}
      />
    </motion.div>
  );
}

function WIPHeader() {
  return (
    <header className="WIP">
      <motion.span
        transition={{ repeat: Infinity, duration: 2 }}
        animate={{ rotate: [-25, 25, -25] }}
      >
        Work in progress.
      </motion.span>
    </header>
  );
}

function ActivitySuggestion(props: { thingToDo: string | undefined }) {
  const { thingToDo } = props;
  return (
    <div className="activity-suggestion">
      However, while I build this, why don't you...
      {thingToDo && (
        <motion.div initial={{ y: 500 }} animate={{ y: 0 }}>
          {thingToDo}?
        </motion.div>
      )}
    </div>
  );
}

const Colors = {
  black: "#0c090d",
  red: "#e01a4f",
  orange: "#f15946",
  yellow: "#f9c22e",
  blue: "#53b3cb",
};
